<template>
  <v-modal
      name="subscription"
      class="modal"
      height="auto"
      :width="450"
      close-button
      @before-open="data = $event.params.data"
      @closed="stopPolling"
  >
    <in-app-product-header
      :icon="data.icon_uri"
      :name="$t(data.reseller ? 'SUBSCRIPTION_NAME_WITH_RESELLER' : 'SUBSCRIPTION_NAME',
        { name: data.name, provider: data.provider, reseller: data.reseller }
      )"
      :description="data.description"
    />

    <ul class="info">
      <li>
        <strong>{{ $t('STATUS') }}</strong>
        <span
          class="status g-status"
          :class="/active|cancelled/.test(data.status) ? 'g-status_success' : 'g-status_error'"
        >
          {{ data.status_translated }}
        </span>
      </li>
      <li>
        <strong>{{ $t('TYPE') }}</strong>
        <span>{{ data.type_translated }}</span>
      </li>
      <li>
        <strong>{{ $t('DEVICE') }}</strong>
        <span>{{ data.nice_device_model }}</span>
      </li>
      <li>
        <strong>{{ $t('PRICE') }}</strong>
        <span>{{ formatCurrency(data.price_cents_eur) }}</span>
      </li>
      <li>
        <strong>{{ expirationLabel }}</strong>
        <span>{{ new Date(data.expires_at * 1000).toLocaleDateString() }}</span>
      </li>
      <li>
        <a :href="data.support_uri" target="_blank">{{ $t("GET_SUPPORT") }}</a>
        <template v-if="data.can_be_cancelled">
          |
          <component
            :is="data.cancel_uri ? 'a' : 'button'"
            class="g-pseudo-link cancel-button"
            :href="data.cancel_uri"
            @click.prevent="cancelSubscription"
          >
            {{ $t('CANCEL') }}

            <loader v-if="isCancelling" class="cancel-button__loader" size="13px" border="3px" />
          </component>
        </template>
      </li>
    </ul>
  </v-modal>
</template>

<script>
import VModal from '@/components/VModal.vue';
import InAppProductHeader from '@/components/InAppProductHeader.vue';
import formatCurrency from '@/utils/formatCurrency';
import Loader from '@/components/Loader';

export default {
  components: {
    VModal,
    InAppProductHeader,
    Loader,
  },
  data() {
    return {
      isCancelling: false,
      data: {},
      pollingTimeout: null,
    };
  },
  watch: {
    data() {
      this.isCancelling = false;
    },
  },
  computed: {
    expirationLabel() {
      if (this.data.status === 'active') {
        return this.$t('SUBSCRIPTION_RENEWS');
      }

      if (Date.now() < new Date(this.data.expires_at * 1000)) {
        return this.$t('SUBSCRIPTION_EXPIRES');
      }

      return this.$t('SUBSCRIPTION_EXPIRED');
    },
  },
  methods: {
    formatCurrency,
    async cancelSubscription() {
      if (this.isCancelling || !this.data.can_be_cancelled) {
        return;
      }

      await this.$confirm(this.$t('SUBSCRIPTION_CANCEL_CONFIRMATION'), { cancelText: this.$t('NO'), okText: this.$t('YES_CANCEL') });
      if (this.data.cancel_uri) {
        window.location.href = this.data.cancel_uri;
        return;
      }

      this.isCancelling = true;
      try {
        await this.$api.requestCancelSubscription({ identifier: this.data.purchase_identifier });
        this.pollSubscriptionStatus();
      } catch {
        this.isCancelling = false;
      }
    },
    async pollSubscriptionStatus() {
      const interval = 1000;
      const timeout = 30000;
      const endTime = Date.now() + timeout;

      const checkStatus = async () => {
        if (Date.now() > endTime) {
          this.isCancelling = false;
          this.$notify({
            type: 'error',
            text: this.$t('SUBSCRIPTION_CANCEL_FAILED'),
          });
          return;
        }

        try {
          const subscriptions = await this.$api.getSubscriptions();
          const currentSubscription = subscriptions.find(sub => sub.purchase_identifier === this.data.purchase_identifier);

          if (currentSubscription.status === 'cancelled') {
            this.isCancelling = false;
            this.data = currentSubscription;
            this.$notify({
              type: 'success',
              text: this.$t('SUBSCRIPTION_CANCELLED'),
            });
            this.$emit('subscription-updated', currentSubscription);
            return;
          }
        } catch {
          //
        }

        this.pollingTimeout = setTimeout(checkStatus, interval);
      };

      checkStatus();
    },
    stopPolling() {
      if (this.pollingTimeout) {
        clearTimeout(this.pollingTimeout);
        this.pollingTimeout = null;
      }
    },
  },
  beforeDestroy() {
    this.stopPolling();
  },
};
</script>

<style lang="scss" scoped>
.modal {
  padding: 15px;
}

.header {
  margin-bottom: 15px;
}

.support {
    position: relative;
    top: 2px;
    left: 1px;
}

.status {
    font-size: 14px;
    word-break: break-word;
}

.status,
.type {
    word-break: break-word;
}

.info {
  margin-top: 15px;

  li:not(:last-child) {
    margin-bottom: 10px;
  }

  strong:first-child {
    margin-right: 5px;
  }
}

.cancel-button {
  display: inline-flex;
  align-items: center;
  gap: 5px;

  &__loader {
    width: auto;
  }
}
</style>
